import axios from "axios";
import { baseUrls, normalLogin } from "./ApisUrls";

export async function getprams() {
  const token = localStorage.getItem("ACCESS_TOKEN");
  const ACCESS_TOKEN = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const api = baseUrls;
  const data = await axios.get(api, ACCESS_TOKEN);
  const redData = await data.data;
  console.log(redData);
  return redData;
}

export async function userLogin(username, password) {
  let body;
  let resData;
  const options = {
    headers: { "content-type": "application/json" },
  };
  if (["@"].some((a) => a !== username)) {
    body = {
      mobileNumber: username,
      password: password,
    };
  } else {
    body = {
      email: username,
      password: password,
    };
  }
  const data = JSON.stringify(body);
  await axios
    .post(normalLogin + "sign-in", data, options)
    .then(function (response) {
      console.log("res" + response.data);
      resData = response.data;
    })
    .catch(function (error) {
      console.log(error);
    });
  return resData;
}

export async function listofAgent() {
  let listAgent;
  const tokenUrl = localStorage.getItem("ACCESS_TOKEN");
  const ACCESS_TOKENHeader = {
    headers: {
      Authorization: `Bearer ${tokenUrl}`,
    },
  };

  await axios
    .get(
      "https://meta.oxyloans.com/api/student-service/student/list-of-agents",
      ACCESS_TOKENHeader
    )
    .then((resp) => {
      listAgent = resp.data;
      console.log(resp.data);
    });
  return listAgent;
}

export async function listofAgentsecondmore(usertype) {
  let agentbody;
  let agentDatatwo;
  let urlpostdata;

  const to = localStorage.getItem("ACCESS_TOKEN");
  const options = {
    headers: {
      "content-type": "application/json",
      Authorization: `Bearer ${to}`,
    },
  };
  if (usertype === "loading") {
    agentbody = {};
    urlpostdata = "list-of-unique-agents-updated";
  } else {
    agentbody = {
      agentName: usertype,
    };
    urlpostdata = "list-of-unique-universities-updated";
  }

  const data = JSON.stringify(agentbody);
  await axios
    .post(
      "https://meta.oxyloans.com/api/student-service/student/" + urlpostdata,
      data,
      options
    )
    .then(function (response) {
      agentDatatwo = response.data;
    })
    .catch(function (error) {
      console.log(error);
    });

  return agentDatatwo;
}

