import React, { Component } from "react";
import SweetAlert from "sweetalert2-react";
import "react-datepicker/dist/react-datepicker.css";
import { TabContent, TabPane, Nav, NavLink, NavItem } from "reactstrap";
import classnames from "classnames";
import { listofAgent } from "../../../OxyJs/Afterlogin";
import { Spinner } from "reactstrap";

export default class Professors extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 1,
      show: false,
      listAgent: [],
    };
  }
  componentDidMount() {
    listofAgent()
      .then((response) => {
        this.setState({
          listAgent: response,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }
  render() {
    const { activeTab, listAgent } = this.state;
    const { history } = this.props;
    return (
      <>
        <div className="section-body">
          <div className="container-fluid">
            <div className="d-flex justify-content-between align-items-center ">
              <div className="header-action">
                <h1 className="page-title">Agents</h1>
              </div>
            </div>
          </div>
        </div>
        <div className="section-body mt-4">
          <div className="container-fluid">
            <TabContent activeTab={activeTab}>
              <TabPane tabId={1} className={classnames(["fade show"])}>
                <div className="table-responsive">
                  <table className="table table-hover table-vcenter table_custom text-nowrap spacing5 border-style mb-0">
                    <tbody>
                      {console.log(listAgent)}
                      {listAgent.length == 0 ? (
                        <Spinner
                          color="primary"
                          className="m-5 text-center"
                          style={{
                            position: "absolute",
                            left: "40%",
                            top: "80px",
                          }}
                        >
                          Loading...
                        </Spinner>
                      ) : (
                        listAgent.map((data) => (
                          <tr
                            key={data.agentName}
                            onClick={() => {
                              history.push(
                                `/courses?agentName=${data.agentName}`
                              );
                            }}
                          >
                            <td>
                              <div className="font-15">{data.agentName}</div>
                            </td>
                            <td>
                              <div className="font-15 pull-left">
                                {data.countOfUniversitiesMappedToAgent} (Mapped)
                              </div>
                            </td>
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                  <SweetAlert
                    show={this.state.show}
                    title="Are you sure?"
                    text="You will not be able to recover this imaginary file!"
                    type="warning"
                    showCancelButton={true}
                    confirmButtonColor="#dc3545"
                    confirmButtonText="Yes, delete it!"
                    closeOnConfirm={false}
                    onConfirm={() => this.setState({ show: false })}
                  />
                </div>
              </TabPane>
            </TabContent>
          </div>
        </div>
      </>
    );
  }
}
