import React, { Component } from "react";
import SweetAlert from "sweetalert2-react";
import "react-datepicker/dist/react-datepicker.css";
import { TabContent, TabPane } from "reactstrap";
import classnames from "classnames";
import { listofAgentsecondmore } from "../../../OxyJs/Afterlogin";
import Spinner from "reactstrap/lib/Spinner";

export default class Staff extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 1,
      show: false,
      agentUniversities: [],
      agentNameInfo: "",
    };
  }

  componentDidMount() {
    const queryParams = new URLSearchParams(window.location.search);
    const agentName = queryParams.get("agentName");

    if (agentName) {
      this.setState({
        agentNameInfo: agentName,
      });

      listofAgentsecondmore(agentName)
        .then((response) => {
          console.log(response);
          this.setState({
            agentUniversities: response,
          });
        })
        .catch((error) => {
          this.setState({
            agentUniversities: [],
          });
        });
    }
  }

  render() {
    const { activeTab, agentNameInfo } = this.state;
    const { history } = this.props;

    return (
      <>
        <div className="section-body">
          <div className="container-fluid">
            <div className="d-flex justify-content-between align-items-center ">
              <div className="header-action">
                <h1 className="page-title">
                  {agentNameInfo} Agent Universities
                </h1>
              </div>
            </div>
          </div>
        </div>
        <div className="section-body mt-4">
          <div className="container-fluid">
            <TabContent activeTab={activeTab}>
              <TabPane tabId={1} className={classnames(["fade show"])}>
                <div className="card">
                  <div className="table-responsive">
                    {this.state.agentUniversities === 0 ? (
                      <Spinner
                        color="primary"
                        className="m-5 text-center"
                        style={{
                          position: "absolute",
                          left: "40%",
                          top: "80px",
                        }}
                      >
                        Loading...
                      </Spinner>
                    ) : (
                      <table className="table table-hover table-vcenter text-nowrap table-striped mb-0">
                        <thead>
                          <tr>
                            <th>university Name</th>
                            <th>Info</th>
                            {/* <th>Telephone Number</th>
                            <th>country</th>
                            <th>Location In India</th>
                            <th>Email</th>
                            <th>address</th> */}
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.agentUniversities.map((data) => {
                            return (
                              <tr key={Math.random()}>
                                <td className="w60">
                                  {data.universityName
                                    ? data.universityName
                                    : "No Data"}
                                </td>
                                <td className="w60">
                                  <span
                                    className="tag tag-success"
                                    onClick={() => {
                                      history.push(
                                        `/contact?university=${data.universityName}`
                                      );
                                    }}
                                  >
                                    Know more
                                  </span>
                                </td>
                                {/* <td className="w60">
                                  {data.telephoneNumber
                                    ? data.telephoneNumber
                                    : "No Data"}
                                </td>
                                <td className="w60">
                                  {data.country ? data.country : "No Data"}
                                </td>
                                <td className="w60">
                                  {data.locationInIndia
                                    ? data.locationInIndia
                                    : "No Data"}
                                </td>
                                <td className="w60">
                                  {data.emailOrWeb
                                    ? data.emailOrWeb
                                    : "No Data"}
                                </td>
                                <td className="w60">
                                  {data.address ? data.address : "No Data"}
                                </td> */}
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                    )}
                    <SweetAlert
                      show={this.state.show}
                      title="Are you sure?"
                      text="You will not be able to recover this imaginary file!"
                      type="warning"
                      showCancelButton={true}
                      confirmButtonColor="#dc3545"
                      confirmButtonText="Yes, delete it!"
                      closeOnConfirm={false}
                      onConfirm={() => this.setState({ show: false })}
                    />
                  </div>
                </div>
              </TabPane>
            </TabContent>
          </div>
        </div>
      </>
    );
  }
}
