import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Toast, ToastHeader, ToastBody } from "reactstrap";
import { userLogin } from "../../OxyJs/Afterlogin";
import Spinner from "reactstrap/lib/Spinner";
export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      userpassword: "",
      tostIs: false,
      isLogined: false,
      btnDisable: true,
      errormessage: "Enter Valid User Name / Password",
      isloadinSpriner: false,
    };
  }

  handleInputUsername(event) {
    this.setState({
      username: event.target.value,
    });
  }

  handleInputPasword(event) {
    this.setState({
      userpassword: event.target.value,
    });
  }

  loginUserFunction(event) {
    this.setState({
      isloadinSpriner: true,
    });
    if (this.state.username === "" || this.state.userpassword === "") {
      this.setState({
        tostIs: true,
        isloadinSpriner: false,
      });
      return;
    } else {
      event.preventDefault();
      this.setState({
        tostIs: false,
        btnDisable: true,
      });

      userLogin(this.state.username, this.state.userpassword)
        .then((response) => {
          console.log(response);

          if (response === undefined) {
            this.setState({
              isLogined: false,
              tostIs: true,
              errormessage: "internal Error 500",
              isloadinSpriner: false,
            });
          } else {
            this.setState({
              isLogined: true,
            });
            localStorage.setItem("ACCESS_TOKEN", response.accessToken);
          }
        })
        .catch((error) => {});
    }
  }

  getUrlParameter(name) {
    name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
    var regex = new RegExp("[\\?&]" + name + "=([^&#]*)");

    var results = regex.exec(this.props.location.search);
    return results === null
      ? ""
      : decodeURIComponent(results[1].replace(/\+/g, " "));
  }

  render() {
    const { history } = this.props;
    const token = this.getUrlParameter("token");
    const error = this.getUrlParameter("error");

    // const queryParams = new URLSearchParams(window.location.search);
    // const token = queryParams.get("token");
    // const error = queryParams.get("error");

    if (token) {
      localStorage.setItem("ACCESS_TOKEN", token);
      if (history) history.push("/pageProfile");
    } else if (error) {
      alert(error);
    }

    if (this.state.isLogined) {
      if (history) history.push("/");
    }

    return (
      <div className="auth option2">
        <div className="auth_left">
          <div className="card">
            <div className="card-body">
              <div className="text-center">
                <Link className="header-brand" to="#">
                  <i className="fa fa-graduation-cap brand-logo"></i>
                </Link>
                <div className="card-title mt-3">Login to your account</div>

                <a href="http://ec2-3-6-94-124.ap-south-1.compute.amazonaws.com:9000/oauth2/authorize/facebook?redirect_uri=https://www.app.studentx.world/login">
                  <button type="button" className="mr-1 btn btn-facebook">
                    <i className="fa fa-facebook mr-2"></i>Facebook
                  </button>
                </a>

                <a href="http://ec2-3-6-94-124.ap-south-1.compute.amazonaws.com:9000/oauth2/authorize/google?redirect_uri=https://www.app.studentx.world/login">
                  <button type="button" className="btn btn-google">
                    <i className="fa fa-google mr-2"></i>Google
                  </button>
                </a>

                <h6 className="mt-3 mb-3">Or</h6>
              </div>
              <div className="form-group">
                <input
                  type="email"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter email / Phone Number"
                  onChange={this.handleInputUsername.bind(this)}
                />
              </div>
              <div className="form-group">
                <label className="form-label">
                  <Link to="forgotpassword" className="float-right small">
                    I forgot password
                  </Link>
                </label>
                <input
                  type="password"
                  className="form-control"
                  id="exampleInputPassword1"
                  placeholder="Password"
                  onChange={this.handleInputPasword.bind(this)}
                />
              </div>
              <div className="form-group">
                <label className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input" />
                  <span className="custom-control-label">Remember me</span>
                </label>
              </div>
              <div className="text-center">
                <Link
                  to="#"
                  className="btn btn-primary btn-block"
                  title=""
                  onClick={this.loginUserFunction.bind(this)}
                  disable={this.state.btnDisable}
                >
                  Sign in{" "}
                  {this.state.isloadinSpriner ? (
                    <Spinner
                      color="primary"
                      size="sm"
                      style={{ marginleft: "20px" }}
                    >
                      Loading...
                    </Spinner>
                  ) : (
                    ""
                  )}
                </Link>
                {/* <div className="text-muted mt-4">
                  Don't have account yet? <Link to="/signup">Sign Up</Link>
                </div> */}
              </div>
            </div>
          </div>
        </div>
        <div
          className="p-3 my-2 rounded"
          style={{ position: "absolute", top: "10px", right: "20px" }}
        >
          <Toast isOpen={this.state.tostIs}>
            <ToastHeader icon="danger">Error </ToastHeader>
            <ToastBody>{this.state.errormessage}</ToastBody>
          </Toast>
        </div>
      </div>
    );
  }
}
