import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class SignUp extends Component {
  // getUrlParameter(name) {
  //   name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
  //   var regex = new RegExp("[\\?&]" + name + "=([^&#]*)");

  //   var results = regex.exec(this.props.location.search);
  //   return results === null
  //     ? ""
  //     : decodeURIComponent(results[1].replace(/\+/g, " "));
  // }

  render() {
    const { history } = this.props;

    const queryParams = new URLSearchParams(window.location.search);
    const token = queryParams.get("token");
    const error = queryParams.get("error");

    // const token = this.getUrlParameter("token");
    // const error = this.getUrlParameter("error");

    if (token) {
      localStorage.setItem("ACCESS_TOKEN", token);
      if (history) history.push("/pageProfile");
    } else if (error) {
      alert(error);
    }
    return (
      <div className="auth option2">
        <div className="auth_left">
          <div className="card">
            <div className="card-body">
              <div className="text-center">
                <Link className="header-brand" to="#">
                  <i className="fa fa-graduation-cap brand-logo"></i>
                </Link>
                <div className="card-title">Create new account</div>

                <a href="http://ec2-3-6-94-124.ap-south-1.compute.amazonaws.com:9000/oauth2/authorize/facebook?redirect_uri=https://www.app.studentx.world/login">
                  <button type="button" className="mr-1 btn btn-facebook">
                    <i className="fa fa-facebook mr-2"></i>Facebook
                  </button>
                </a>

                <a href="http://ec2-3-6-94-124.ap-south-1.compute.amazonaws.com:9000/oauth2/authorize/google?redirect_uri=https://www.app.studentx.world/login">
                  <button type="button" className="btn btn-google">
                    <i className="fa fa-google mr-2"></i>Google
                  </button>
                </a>
                <h6 className="mt-3 mb-3">Or</h6>
              </div>
              <div className="form-group">
                <label className="form-label">Name</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Enter name"
                />
              </div>
              <div className="form-group">
                <label className="form-label">Email address</label>
                <input
                  type="email"
                  className="form-control"
                  placeholder="Enter email"
                />
              </div>
              <div className="form-group">
                <label className="form-label">Password</label>
                <input
                  type="password"
                  className="form-control"
                  placeholder="Password"
                />
              </div>
              <div className="form-group">
                <label className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input" />
                  <span className="custom-control-label">
                    Agree the <Link to="/">terms and policy</Link>
                  </span>
                </label>
              </div>
              <div className="text-center">
                <button type="submit" className="btn btn-primary btn-block">
                  Create new account
                </button>
                <div className="text-muted mt-4">
                  Already have account? <Link to="/login">Sign in</Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
