import React, { Component } from "react";
import SweetAlert from "sweetalert2-react";
import "react-datepicker/dist/react-datepicker.css";
import { TabContent, TabPane } from "reactstrap";
import classnames from "classnames";
import { listofAgentsecondmore } from "../../../OxyJs/Afterlogin";
import Spinner from "reactstrap/lib/Spinner";

export default class Departments extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 1,
      isCardRemove: false,
      isCollapsed: false,
      show: false,
      getData: [],
    };
  }

  componentDidMount() {
    listofAgentsecondmore("loading")
      .then((response) => {
        console.log(response);
        if (response === undefined) {
        } else {
          this.setState({
            getData: response,
            isloading: false,
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
  render() {
    const { activeTab } = this.state;
    const { history } = this.props;
    return (
      <>
        <div className="section-body">
          <div className="container-fluid">
            <div className="d-flex justify-content-between align-items-center">
              <div className="header-action">
                <h1 className="page-title">Agents</h1>
              </div>
              {/* <Nav tabs className="page-header-tab">
                <NavItem>
                  <NavLink
                    className={classnames({ active: activeTab === 1 })}
                    onClick={() => this.setState({ activeTab: 1 })}
                  >
                    View
                  </NavLink>
                </NavItem>
              </Nav> */}
            </div>
          </div>
        </div>
        <div className="section-body mt-4">
          <div className="container-fluid">
            <TabContent activeTab={activeTab}>
              <TabPane tabId={1} className={classnames(["fade show"])}>
                <div className="table-responsive">
                  <div className="table-responsive card">
                    {this.state.isloading == true ? (
                      <Spinner
                        color="primary"
                        className="m-5 text-center"
                        style={{
                          position: "absolute",
                          left: "40%",
                          top: "80px",
                        }}
                      >
                        Loading...
                      </Spinner>
                    ) : (
                      <table className="table table-hover table-striped table-vcenter text-nowrap mb-0">
                        <thead>
                          <tr>
                            <th>Agent</th>
                            <th>Tied Up universities </th>
                          </tr>
                        </thead>
                        <tbody>
                          {console.log(this.state.getData)}

                          {this.state.getData.map((data) => {
                            return (
                              <tr
                                key={Math.random()}
                                onClick={() => {
                                  history.push(
                                    `/staff?agentName=${data.agentName}`
                                  );
                                }}
                              >
                                <td>{data.agentName}</td>
                                <td>{data.countOfUniversitiesMappedToAgent}</td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                    )}
                    <SweetAlert
                      show={this.state.show}
                      title="Are you sure?"
                      text="You will not be able to recover this imaginary file!"
                      type="warning"
                      showCancelButton={true}
                      confirmButtonColor="#dc3545"
                      confirmButtonText="Yes, delete it!"
                      closeOnConfirm={false}
                      onConfirm={() => this.setState({ show: false })}
                    />
                  </div>
                </div>
              </TabPane>
            </TabContent>
          </div>
        </div>
      </>
    );
  }
}
